import React, { FC } from "react";

import { colors, widthSizes } from "../../styleguide";

import Icon, { IconName } from "./Icon";
import { StyledButton, StyledButtonProps } from "./LegacyButton.styles";

interface ButtonProps extends StyledButtonProps {
	dataQA: string
	ariaLabel: string
	text?: React.ReactNode | string
	type?: "submit" | "reset" | "button"
	onClick?: React.MouseEventHandler<HTMLButtonElement>
	isDisabled?: boolean
	icon?: IconName | FC
	iconColor?: string
	customIconColor?: boolean
	iconSize?: number
	iconStrokeWidth?: number
	tabindex?: number
	className?: string
}
/** @deprecated this component has been deprecated */
const LegacyButton: FC<ButtonProps> = ({
	dataQA,
	ariaLabel,
	text,
	variant = "default",
	widthSize = widthSizes.m,
	rightAlignIcon = false,
	isDisabled = false,
	type = "button",
	onClick = () => {},
	color = colors.white,
	backgroundColor = colors.midBlue,
	borderColor = "transparent",
	disabledBackgroundColor = colors.darkGray,
	disabledTextColor = colors.lightGray,
	hoverBackgroundColor = colors.midBlue,
	hoverTextColor = colors.white,
	icon: IconComponent,
	iconColor = colors.white,
	iconSize = 14,
	iconStrokeWidth = 2,
	customIconColor = false,
	className,
	tabindex = 0,
	mt,
	mb,
	mr,
	ml,
}) => {
	const isIconButton = widthSize === widthSizes.icon;

	return (
		<StyledButton
			aria-label={ariaLabel}
			type={type}
			data-qa={dataQA}
			variant={variant}
			onClick={onClick}
			disabled={isDisabled}
			widthSize={widthSize}
			rightAlignIcon={rightAlignIcon}
			color={color}
			backgroundColor={backgroundColor}
			borderColor={borderColor}
			disabledBackgroundColor={disabledBackgroundColor}
			disabledTextColor={disabledTextColor}
			hoverBackgroundColor={hoverBackgroundColor}
			hoverTextColor={hoverTextColor}
			tabIndex={tabindex}
			mt={mt}
			mb={mb}
			mr={mr}
			ml={ml}
			className={className}
		>
			{/* eslint-disable-next-line no-nested-ternary */}
			{typeof IconComponent === "string" ? (
				<Icon
					variant={variant}
					icon={IconComponent}
					iconColor={iconColor}
					customIconColor={customIconColor}
					size={iconSize}
					stroke={iconStrokeWidth}
				/>
			) : IconComponent
				? <IconComponent />
				: <></>}
			{!isIconButton && text}
		</StyledButton>
	);
};

export default LegacyButton;
